import { Container, Row, Button } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHouse } from "@fortawesome/free-solid-svg-icons";
export default function ErrorPage({ error }) {
  console.log("hit errorpage");
  console.log(error.message, error.cause, error.status);

  return (
    <Container
      id="error-page"
      className="contr"
      fluid
      style={{
        justifyContent: "center",
        alignContent: "center",
        fontSize: "3rem",
      }}
    >
      <Button
        href={window.location.origin + "/#/"}
        className="nav-icon"
        style={{ position: "absolute", top: "5vh", left: "5vw" }}
        id={`nav_home`}
        onClick={(e) =>
          setTimeout(() => {
            window.location.reload();
          }, 0)
        }
      >
        <FontAwesomeIcon icon={faHouse} title={`Home`} size="2x" />
      </Button>
      <Row>
        <h2 style={{ fontSize: "3rem" }}>Oops!</h2>
      </Row>
      <Row>
        <p>An unexpected error has occurred.</p>
        <p>{error.cause || null}</p>
        <p>{error.message}</p>
      </Row>
    </Container>
  );
}
